import styles from './index.less';
import React from 'react';
import PropTypes from 'prop-types';

import unitAction from '../../../actions';

import { Layout, Menu, Icon, Button } from 'antd';

const { Sider } = Layout;
const { SubMenu } = Menu;
const { MenuItemGroup } = Menu;

class UnitsList extends React.Component {
  static propTypes = {};

  constructor(props) {
    super(props);
  }

  handleClick(e) {
    e && e.key && unitAction.addUnit(e.key);
  }

  render() {
    return (
      <Sider trigger={null} collapsible breakpoint="lg" width={256}>
        <div className="logo" key="logo">
          <h1></h1>
        </div>
        <Menu theme="dark" mode="inline" onClick={this.handleClick}>
          <SubMenu
            title={
              <span>
                <Icon type="book" />
              </span>
            }
          >
            <Menu.Item key="TITLE">
              <Icon type="ellipsis" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="BUTTON">
              <Icon type="link" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="TEXTBODY">
              <Icon type="file-text" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="LINE">
              <Icon type="minus" />
              <span></span>
            </Menu.Item>
            {/* <Menu.Item key="RULES">
              <Icon type="exception" />
              <span></span>
            </Menu.Item> */}
            {/* <Menu.Item key="OGP">
              <Icon type="team" />
              <span></span>
            </Menu.Item> */}
          </SubMenu>
          <SubMenu
            title={
              <span>
                <Icon type="video-camera" />
              </span>
            }
          >
            <Menu.Item key="IMAGE">
              <Icon type="picture" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="AUDIO">
              <Icon type="sound" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="BOTTOMFLOAT">
              <Icon type="down" />
              <span></span>
            </Menu.Item>
          </SubMenu>
          <SubMenu
            title={
              <span>
                <Icon type="api" />
              </span>
            }
          >
            <Menu.Item key="CAROUSEL">
              <Icon type="switcher" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="IMAGEGIRD">
              <Icon type="appstore-o" />
              <span></span>
            </Menu.Item>
          </SubMenu>
          <SubMenu
            title={
              <span>
                <Icon type="database" />
              </span>
            }
          >
            <Menu.Item key="DATASYSTEM">
              <Icon type="area-chart" />
              <span></span>
            </Menu.Item>
            {/* <Menu.Item key="NETS">
              <Icon type="bar-chart" />
              <span></span>
            </Menu.Item> */}
          </SubMenu>
          <SubMenu
            title={
              <span>
                <Icon type="code" />
              </span>
            }
          >
            <Menu.Item key="QRCODE">
              <Icon type="qrcode" />
              <span></span>
            </Menu.Item>
            <Menu.Item key="CODE">
              <Icon type="gitlab" />
              <span></span>
            </Menu.Item>
          </SubMenu>
        </Menu>
      </Sider>
    );
  }
}

export default UnitsList;



