import './index.less';

import React from 'react';
import PropTypes from 'prop-types';
import ImmutablePropTypes from 'react-immutable-proptypes';
import immutable from 'immutable';
import reactCSS from 'reactcss';

import unitAction from '../../../actions';
import UnitPanel from '../../ui/unitPanel';

import { Icon, Row, Col, Button, Radio, Input, InputNumber, Collapse, Upload, message } from 'antd';
const Panel = Collapse.Panel;
const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class UnitCarousel extends React.PureComponent {
  static propTypes = {
    data: ImmutablePropTypes.map,
    id: PropTypes.number
  };

  constructor(props) {
    super(props);
    const { data } = this.props;
    const me = this;
    this.state = {
      contents: data.get('contents').toJS() || [],
      uploadProps: {
        name: 'file',
        action: '/api/v1/common/upload',
        accept: 'image/*',
        onChange({ file, fileList, event }) {
          if (file.status !== 'uploading') {
            console.log('...');
          }
          if (file.status === 'done') {
            const { code, data } = file.response;
            if (code === 200) {
              const { files } = data;
              message.success(`${file.name} `);
              if (files[0].index > -1) {
                me.changeContent(files[0].index, 'address', files[0].url);
              }
            } else {
              message.error(`${file.response.message}`);
            }
          } else if (file.status === 'error') {
            message.error(`${file.name} `);
          }
        }
      }
    };
  }

  addContent = () => {
    const { id } = this.props;
    const { contents } = this.state;
    contents.push({ address: '', url: '' });
    unitAction.editUnit(id, 'contents', immutable.fromJS(contents));
  };

  changeContent = (index, type, value) => {
    const { id } = this.props;
    const { contents } = this.state;
    contents[index][type] = value;
    unitAction.editUnit(id, 'contents', immutable.fromJS(contents));
  };

  removeContent = (index) => {
    const { id } = this.props;
    const { contents } = this.state;
    contents.splice(index, 1);
    unitAction.editUnit(id, 'contents', immutable.fromJS(contents));
  };

  renderContents(contents) {
    const { uploadProps } = this.state;
    return contents.map((item, index) => {
      uploadProps.action = `/api/v1/common/upload?index=${index}`;
      return (
        <Panel
          key={index}
          header={
            <div>
              <span>{index + 1}</span>
              <Icon type="close" onClick={e => this.removeContent(index)} />
            </div>
          }
        >
          <Row type="flex" justify="left" align="middle" gutter={16}>
            <Col className="title" span={4}>
              
            </Col>
            <Col span={14}>
              <Input
                type="text"
                placeholder=""
                value={item.address}
                onChange={e => this.changeContent(index, 'address', e.target.value)}
              />
            </Col>
            <Col span={4}>
              <Upload {...uploadProps}>
                <Button>
                  <Icon type="upload" /> 
                </Button>
              </Upload>
            </Col>
          </Row>
          <Row type="flex" justify="left" align="middle" gutter={16}>
            <Col className="title" span={4}>
              
            </Col>
            <Col span={14}>
              <Input
                type="text"
                placeholder=""
                defaultValue={item.url}
                onBlur={e => this.changeContent(index, 'url', e.target.value)}
              />
            </Col>
          </Row>
        </Panel>
      );
    });
  }

  render() {
    const { data, id } = this.props;
    const { contents } = this.state;
    const styles = reactCSS({ default: {} });
    return (
      <UnitPanel unitId={id} unitName={data.get('name')}>
        <Collapse>{this.renderContents(contents)}</Collapse>
        <Row type="flex" justify="left" align="middle" gutter={16}>
          <Col span={24}>
            <Button type="primary" onClick={this.addContent}>
              <Icon type="plus" />
            </Button>
          </Col>
        </Row>
        <Row type="flex" justify="left" align="middle" gutter={16}>
          <Col className="title" span={4}>
            
          </Col>
          <Col span={20}>
            <RadioGroup value={data.get('effect')} onChange={e => unitAction.editUnit(id, 'effect', e.target.value)}>
              <RadioButton value="slide"></RadioButton>
              <RadioButton value="fade"></RadioButton>
              <RadioButton value="cube"></RadioButton>
              <RadioButton value="coverflow">3d</RadioButton>
              <RadioButton value="flip">3d</RadioButton>
            </RadioGroup>
          </Col>
        </Row>
        <Row type="flex" justify="left" align="middle" gutter={16}>
          <Col className="title" span={4}>
            
          </Col>
          <Col span={14}>
            <InputNumber
              min={0}
              defaultValue={1}
              value={data.get('autoplay')}
              onChange={value => unitAction.editUnit(id, 'autoplay', parseFloat(value))}
            />
          </Col>
        </Row>
      </UnitPanel>
    );
  }
}

export default UnitCarousel;



